import React, { PureComponent } from "react";

import Counter from "../../../components/Counter";

class ClassCounterContainer extends PureComponent {
  // PuereComponent   shouldComponentUpdate
  constructor() {
    super();

    this.state = {
      countValue: 0,
    };

    this.handleReset = this.handleReset.bind(this); //  arrow Fn

    //console.log("Conctuctor");
  }

  handleIncremet = () => {
    this.setState((state) => ({ countValue: state.countValue + 1 })); // return 
  };

  handleDecrement = () => {
    if (this.state.countValue > 0) {
      this.setState((state) => {
        // return 
        return {
          countValue: state.countValue - 1,
        };
      });
    }
  };

  handleReset() {
    //  arrow Fn
    this.setState({
      countValue: 0,
    });
  }

  render() {
    //console.log("render");
    return (
      <Counter
        countValue={this.state.countValue}
        onIncrement={this.handleIncremet}
        onDecrement={this.handleDecrement}
        onReset={this.handleReset}
      />
    );
  }
}

export default ClassCounterContainer;

